


.navbar-logo{
  text-align: center;
  padding-top: 10px;
}

.fa-react {
  margin-left: 0.5rem;
  font-size: 1.6rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 70vw;
  justify-content: end;
  margin-right: 2rem;
  margin-bottom: 0;
}

.nav-links {
  color: white !important;
  text-decoration: none;
  padding: 0.5rem 1rem;
}

.nav-links:hover {
  background-color: #6d76f7;
  border-radius: 4px;
  transition: all 0.2s ease-out;
  text-decoration: none;
}

.fa-bars {
  color: #fff;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

.cart-mobile {
  position: absolute;
  right: 20%;
  list-style: none;
}

@media screen and (min-width: 960px) {
  .cart-mobile {
    display: none;
  }
}

.cart-desktop {
  color: white;
}
@media screen and (max-width: 960px) {
  .cart-mobile {
    right: 10%;
  }
  .cart-desktop {
    display: none;
  }
 

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #6668f4;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 2;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: #7577fa;
    border-radius: 0;
  }

  .navbar-logo {
    position: relative;
    transform: none
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    padding: 1.5rem;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    background: #4ad9e4;
    text-decoration: none;
    color: #fff;
    font-size: 1.5rem;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #6568f4;
    transition: 250ms;
  }

  Button {
    display: none;
  }
  .title-banner {
    padding-top: 12%;
    font-size: 48px;
  }

  .navbar-logo {
    top: 17%;
    transform: none;
    left: 0%;
  }
}

@media only screen and (max-width: 768px) {
  .title-banner {
    padding-top: 9%;
    font-size: 42px;
  }
  .banner-description {
    font-size: 34px;
    margin-bottom: 0px;
  }

  .btn-adqui {
    font-size: 21px;
  }

  .navbar-logo {
    top: 17%;
    transform: none;
    left: 0%;
  }
  .cart-mobile {
    right: 11%;
  }
}
@media only screen and (max-width: 700px) {
  .title-banner {
    padding-top: 9%;
    font-size: 38px;
  }
}

@media only screen and (max-width: 576px) {
  .navbar-logo  img{
    padding: 5px 35px;
  }
  .cart-mobile {
    right: 15%;
  }

  .title-banner {
    padding-top: 8%;
    font-size: 32px;
  }
  .banner-description {
    font-size: 26px;
    margin-bottom: 7px;
  }
  .btn-adqui {
    font-size: 16px;
    border: 1px solid white;
  }
}

@media only screen and (max-width: 500px) {
  .title-banner {
    padding-top: 8%;
    font-size: 22px;
  }
  .banner-description {
    font-size: 21px;
    margin-bottom: 7px;
  }
  .btn-adqui {
    font-size: 16px;
    border: 1px solid white;
  }
}

@media only screen and (max-width: 400px) {
  .cart-mobile {
    right: 20%;
  }

  .navbar-logo img {
    width: 100%;
  }
  .NavbarItems {
    height: 65px;
  }
  .menu-icon {
    top: 25%;
    right: 5%;
    transform: none;
  }
  .title-banner {
    padding-top: 10%;
    font-size: 15px;
  }
  .banner-description {
    font-size: 13px;
    margin-bottom: 9px;
  }
  .btn-adqui {
    font-size: 10px;
    border: 1px solid white;
    display: inline;
    padding: 5px;
  }
}
